body {
  margin: 0;
  padding: 0;
}

h1, h2, h3, p {
  margin: 0;
}

@media only screen and (max-width: 400px) {
  .container {
    width: 350px;
  }
}

@media only screen and (min-width: 401px) and (max-width: 850px) {
  .container {
    width: 350px;
  }
}

@media only screen and (min-width: 851px) {
  .container {
    width: 650px;
  }
}

.container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.counter {
  width: 100%;
}

.players {
  width: 100%;
  flex-direction: row;
  display: flex;
}

.player {
  width: 50%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.game {
  height: 650px;
  width: 100%;
  flex-wrap: wrap;
  display: flex;
  position: relative;
}

.backdrop {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  background: rgba(255, 255, 255, .37);
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 4px 30px rgba(0, 0, 0, .1);
}

.hidden {
  display: none;
}

.bunner {
  width: 70%;
  height: 40%;
  background-color: #fff;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 30px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px;
}

@media only screen and (max-width: 850px) {
  .box {
    font-size: 120px;
  }
}

@media only screen and (min-width: 851px) {
  .box {
    font-size: 250px;
  }
}

.box {
  width: 33.3333%;
  height: 33.3333%;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  display: flex;
}

.restart {
  width: 60%;
  height: 40%;
  cursor: pointer;
  font-size: 20px;
}

.green {
  background-color: green;
}

.yellow {
  background-color: #ff0;
}

.js-box4 {
  border: 2px solid #000;
}

.js-box1, .js-box7 {
  border-left: 2px solid #000;
  border-right: 2px solid #000;
}

.js-box3, .js-box5 {
  border-top: 2px solid #000;
  border-bottom: 2px solid #000;
}

